import React from 'react'
import '../../styles/Portfolio.scss'
import Slider from 'react-slick'
import Head from 'next/head'



const Portfolio = () => {
  const settings = {
    dots: true,
    infinite: true,
    speed: 500,
    slidesToShow: 1,
    slidesToScroll: 1
  };
  return (
    <div className='Portfolio'>
      <Head>
      <link rel="stylesheet" type="text/css" charset="UTF-8" href="https://cdnjs.cloudflare.com/ajax/libs/slick-carousel/1.6.0/slick.min.css" />
      <link rel="stylesheet" type="text/css" href="https://cdnjs.cloudflare.com/ajax/libs/slick-carousel/1.6.0/slick-theme.min.css" />
      <title>Westenskow Dev</title>
    </Head>
      <h1 className="section-label">
        Portfolio
      </h1>
      <div className="portfolio-content">
        <div className="portfolio-item">
          <h2 className='portfolio-item-title'>vote2play</h2>
          <Slider {...settings}>
            <div className="carousel-item">
              <img src="https://socialplaylists.s3-us-west-1.amazonaws.com/Hero+Images/groups+example.PNG" alt="vote2play groups" className='carousel-image'/>
            </div>
            <div className="carousel-item">
              <img src="https://socialplaylists.s3-us-west-1.amazonaws.com/Hero+Images/Playlist+example.PNG" alt="vote2play playlist" className='carousel-image'/>
            </div>
          </Slider>
        </div>
        <div className="portfolio-item">
          <h2 className="portfolio-item-title">fin-s</h2>
        </div>
      </div>
    </div>
  )
}

export default Portfolio